import { Injectable } from '@angular/core';
import {HttpClient} from "@angular/common/http";
import {Router} from "@angular/router";
import {HttpHeaders} from "@angular/common/http";

@Injectable({
  providedIn: 'root'
})
export class TodoListServiceService {

  constructor(private http: HttpClient) { }

  login(username, password) {
    let url = '\checkIfValid';
    let authHeader = new HttpHeaders().set('Content-Type', 'application/json');
    authHeader = authHeader.set('charset', 'utf-8');
    let options = {headers:authHeader};
    let a = '{"username" :"' + username + '","password" :"' + password + '" ,"userId" :"' + -1 + '"}';
    return this.http.post(url,a,options);
  }
}
